<template>
  <header class="header">
    <button class="header__btn right" @click="logoutUser">Выйти</button>
  </header>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Header",
  methods: {
    logoutUser() {
      this.logout();
    },
    ...mapActions(["logout"])
  }
};
</script>

<style lang="scss">
.header {
  position: fixed;
  padding: 0px 15px;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: #fff;
  box-shadow: 1px 1px 6px black;
  z-index: 10;

  &__btn {
    border: none;
    background-color: #fff;
    height: 100%;
    padding: 20px 10px;
    transition: 0.4s;
    cursor: pointer;

    &:hover {
      background-color: rgb(172, 172, 172);
    }
  }
}

.right {
  float: right;
}
</style>
